import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mcg/planoContaLalurA';

const PlanoContaLalurAIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
          {
            dataField: 'tipoLancamento',
            text: 'Tipo',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'PlanoContaLalurASearchView',
      }}
      label={props.label ? props.label : 'Plano de Contas Lalur A'}
      placeholder={props.placeholder ? props.placeholder : 'Contas Lalur A'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default PlanoContaLalurAIS;
