import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, FormGroup, Label, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputType,
  onlyNumber,
  Tab,
  TabGroup,
  WikiButton,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, PlanoConta } from '../../../../models/dtos/ger/planoConta';
import { PlanoContaSubGrupoParam } from '../../../../models/dtos/ger/planoContaSubGrupoParam';
import { PlanoContaMCG } from '../../../../models/dtos/mcg/planoContaMCG';
import { PLANO_CONTA_NATUREZA_CONTA_ENUM_VALUES } from '../../../../models/enumerated/mcg/planoContaNaturezaContaEnum';
import { existeLancamentoEmContaAnalitica, findSubGrupo } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import GrupoPlanoContaIS from '../../../components/mcg/grupoPlanoContaIS/GrupoPlanoContaIS';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';
import PlanoContaLalurAIS from '../../../components/mcg/planoContaLalurAIS/PlanoContaLalurAIS';
import PlanoContaReferencialIS from '../../../components/mcg/planoContaReferencialIS/PlanoContaReferencialIS';

interface PlanoContaEditViewProps extends EditViewProps<PlanoConta> {}

const PlanoContaEditView: FC<PlanoContaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<PlanoConta>({
    defaultValues: {
      grupoPlanoConta: globalReducer.globalParameter.grupoPlanoConta,
      ...selectedItem,
    },
    mode: 'onBlur',
  });
  const formPropsMCG = useForm<PlanoContaMCG>({
    defaultValues: selectedItem.mcgPlanoConta,
    mode: 'onBlur',
  });

  const [labelFCONT, setLabelFCONT] = useState();
  const [labelSPED, setLabelSPED] = useState();
  const [maxDigitos, setMaxDigitos] = useState<number>();
  const [isContaCaixaDisabled, setIsContaCaixaDisabled] = useState<boolean>();
  const [isNaoIncluirFechamentoDisabled, setIsNaoIncluirFechamentoDisabled] = useState<boolean>();
  const [isAnaliticaDisabled, setIsAnaliticaDisabled] = useState<boolean>(false);
  const { digitoAtivo, digitoPassivo } = globalReducer.globalParameter.grupoPlanoConta;
  const { analitica, conta, id, mcgPlanoConta, grupoPlanoConta } = formProps.getValues();

  useEffect(
    () => {
      preparaSwitches();

      if (id !== null) {
        existeLancamentoEmContaAnalitica(id, globalReducer.globalParameter.empresa.id, {
          thenFunction: result => {
            if (result && analitica) {
              setIsAnaliticaDisabled(true);
            }
          },
        });
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      if (grupoPlanoConta) {
        setMaxDigitos(grupoPlanoConta.mascara.split('#').length - 1);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [grupoPlanoConta]
  );

  useEffect(
    () => {
      buscaLabelFCONT();
      buscaLabelSPED();
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [formPropsMCG.getValues().planoContaReferencial, formPropsMCG.getValues().planoContaReferencialECF]
  );

  const buscaLabelFCONT = () => {
    const { planoContaReferencial } = formPropsMCG.getValues();
    setLabelFCONT(
      planoContaReferencial ? `Instituição: ${planoContaReferencial.instituicao.codigo} - ${planoContaReferencial.instituicao.instituicao}` : ''
    );
  };

  const buscaLabelSPED = () => {
    const { planoContaReferencialECF } = formPropsMCG.getValues();

    setLabelSPED(
      planoContaReferencialECF
        ? `Instituição: ${planoContaReferencialECF.instituicao.codigo} - ${planoContaReferencialECF.instituicao.instituicao}`
        : ''
    );
  };

  const buscaSubGrupo = () => {
    /* tslint:disable:no-string-literal */
    if (formProps.getValues()['isSubGrupo']) {
      const planoContaSubGrupo: PlanoContaSubGrupoParam = {
        conta: formProps.getValues()['isSubGrupo'].conta,
        idGrupoPlanoConta: grupoPlanoConta.id,
        maxDigitos: grupoPlanoConta.mascara.split('#').length - 1,
      };
      findSubGrupo(planoContaSubGrupo, {
        thenFunction: result => {
          if (result) {
            formProps.setValue('conta', result);
            if (!id) {
              formProps.setValue('analitica', result.toString().trim().length === maxDigitos);
            }
          } else {
            alertManager.emit(ACTION_EVENTS.SHOW, {
              message:
                'A máscara configurada para este grupo de plano de contas é menor que o número de dígitos da próxima conta a ser cadastrada. ' +
                'Para alterar a máscara, acesse: Manutenção > Grupos > Plano de Contas',
              onOkClick: () => formProps.setValue('isSubGrupo', undefined),
              type: AlertType.ERROR,
            });
          }
        },
      });
    }
  };

  useEffect(
    () => {
      setIsContaCaixaDisabled(!analitica);
      if (!analitica) {
        formPropsMCG.setValue('contaCaixaSwitch', false);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [analitica]
  );

  const preparaSwitches = () => {
    if (mcgPlanoConta) {
      formPropsMCG.getValues()['contaCaixaSwitch'] = mcgPlanoConta.contaCaixa === 'T';
      formPropsMCG.getValues()['naoIncluirFechamentoSwitch'] = mcgPlanoConta.naoIncluirFechamento === 'T';
      formPropsMCG.getValues()['incorporarLivroCaixaSwitch'] = mcgPlanoConta.incorporarLivroCaixa === 'T';
    }
  };

  useEffect(
    () => {
      if (analitica && digitoAtivo !== conta.charAt(0) && digitoPassivo !== conta.charAt(0)) {
        setIsNaoIncluirFechamentoDisabled(false);
      } else {
        setIsNaoIncluirFechamentoDisabled(true);
        formPropsMCG.setValue('naoIncluirFechamentoSwitch', false);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [analitica, conta]
  );

  const beforePersist = (formData: PlanoConta) => {
    formData.mcgPlanoConta = formPropsMCG.getValues();
    formData.mcgPlanoConta.contaCaixa = formPropsMCG.getValues()['contaCaixaSwitch'] === false ? 'F' : 'T';
    formData.mcgPlanoConta.incorporarLivroCaixa = formPropsMCG.getValues()['incorporarLivroCaixaSwitch'] === false ? 'F' : 'T';
    formData.mcgPlanoConta.naoIncluirFechamento = formPropsMCG.getValues()['naoIncluirFechamentoSwitch'] === false ? 'F' : 'T';

    formData.mcgPlanoConta.grupoPlanoConta = grupoPlanoConta;
  };

  return (
    <WindowComponent title="Manutenção de Plano de Conta" wikiId={1346}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist}>
        <InputGroupInline id="id" formProps={formPropsMCG} hidden={true} />
        <div className="d-none">
          <GrupoPlanoContaIS formProps={formProps} id="grupoPlanoConta" />
        </div>
        <TabGroup formProps={formProps}>
          <Tab title="Geral">
            <PlanoContaIS
              id="isSubGrupo"
              formProps={formProps}
              label="Grupo/Sub-Grupo"
              placeholder="Informe a Conta"
              onChange={buscaSubGrupo}
              sintetica={true}
            />
            <InputGroupInline
              id="codigoReduzido"
              label="Código Reduzido"
              isPrependLabel={true}
              placeholder="Preenchimento opcional - Codificação Automática"
              formProps={formProps}
              type={InputType.NUMBER}
            />
            <InputGroupInline
              id="conta"
              label="Conta"
              isPrependLabel={true}
              placeholder="Número da Conta"
              formProps={{ ...formProps, validation: { ...onlyNumber(0, maxDigitos), required: true } }}
              type={InputType.NUMBER}
            />
            <InputGroupInline
              id="descricao"
              label="Descrição"
              isPrependLabel={true}
              placeholder="Informe a descrição"
              formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
            />
            <CustomInput
              type="switch"
              label="Analítica"
              id="analitica"
              name="analitica"
              innerRef={formProps.register}
              disabled={isAnaliticaDisabled}
            />
          </Tab>
          <Tab title="Configurações Contábeis">
            <FormGroup tag={Row}>
              <Col>
                <CustomInput
                  type="switch"
                  label="Conta Caixa"
                  id="contaCaixaSwitch"
                  name="contaCaixaSwitch"
                  innerRef={formPropsMCG.register}
                  disabled={isContaCaixaDisabled}
                />
              </Col>
            </FormGroup>
            <FormGroup tag={Row}>
              <Col>
                <CustomInput
                  type="switch"
                  label="Incorporar aos Relatórios de Livro Caixa"
                  id="incorporarLivroCaixaSwitch"
                  name="incorporarLivroCaixaSwitch"
                  innerRef={formPropsMCG.register}
                />
              </Col>
            </FormGroup>
            <FormGroup tag={Row}>
              <Col>
                <CustomInput
                  type="switch"
                  label="Não incluir no fechamento do Resultado do Exercício"
                  id="naoIncluirFechamentoSwitch"
                  name="naoIncluirFechamentoSwitch"
                  innerRef={formPropsMCG.register}
                  disabled={isNaoIncluirFechamentoDisabled}
                />
              </Col>
            </FormGroup>
            <Row>
              <Col>
                <InputGroupInline
                  id="configGeralResultadoExercicio"
                  label="Configuração Geral / Natureza da Conta SPED ECF"
                  isPrependLabel={true}
                  formProps={formPropsMCG}
                  inputDropDownProps={{
                    optionsProvider: PLANO_CONTA_NATUREZA_CONTA_ENUM_VALUES,
                  }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Card>
                  <CardBody>
                    <CardTitle>
                      LALUR <WikiButton pageId={180} />
                    </CardTitle>
                    <PlanoContaLalurAIS id="planoContaLalurA" formProps={formPropsMCG} label="Conta Relacionada" />
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Tab>
          <Tab title="SPED ECD/ECF e FCONT">
            <Row>
              <Col>
                <Label>{labelFCONT}</Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <PlanoContaReferencialIS
                  id="planoContaReferencial"
                  formProps={formPropsMCG}
                  label="Plano de Contas Referencial FCONT"
                  onChange={buscaLabelFCONT}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>{labelSPED}</Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <PlanoContaReferencialIS
                  id="planoContaReferencialECF"
                  formProps={formPropsMCG}
                  label="Plano de Contas Referencial SPED ECD/ECF/EFD Contribuições"
                  onChange={buscaLabelSPED}
                />
              </Col>
            </Row>
          </Tab>
        </TabGroup>
      </EditForm>
    </WindowComponent>
  );
};

export default PlanoContaEditView;
