import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/grupoPlanoConta';

const GrupoPlanoContaIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'GrupoPlanoContaSearchView',
      }} // TODO Implementar as regras do IS
      label={props.label ? props.label : 'Grupo de Plano de Contas'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Grupo de Plano de Contas'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default GrupoPlanoContaIS;
