import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mcg/planoContaReferencial';

const PlanoContaReferencialIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'conta',
            text: 'Conta',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
          {
            dataField: 'analitica',
            text: 'Analítica',
          },
          {
            dataField: 'instituicao.instituicao',
            text: 'Instituição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'PlanoContaReferencialSearchView',
      }}
      label={props.label ? props.label : 'Plano de Contas Referencial'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Plano de Contas Referencial'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default PlanoContaReferencialIS;
